import axios from 'axios'
const Post = ({ id }) => <h1>YOU ARE LOOKING AT POST #{id}</h1>;
Post.getInitialProps = async ({ query }) => {
    const res = await axios.get(`https://jsonplaceholder.typicode.com/comments?postId=${query.id}`);
    const { data } = res;
    return query
}
export default Post;

